.headerFourInner {
    display: flex;
    gap: 20px;
    align-items: center;
    .topHeader {
        .topHeaderInner {
            padding-left: 10px;
            align-items: center;
            .language {
                align-items: center;
            }
        }
    }
    .headerFourContainer {
        width: 100%;

        .topHeader {
            background-color: #ffd55f;
            padding: 0;
        }
    }
    .phoneGroup {
        display: flex;
        align-items: center;
        gap: 14px;
        background-color: #73805a;
        padding: 8px 12px;
        .phoneIcon {
            background-color: #fff;
            width: 40px;
            height: 40px;
            padding: 10px;
            border-radius: 50px;
            display: flex;
            align-items: center;
            justify-content: center;
        }
        .phoneNumber {
            span {
                color: #fff;
            }
            p {
                color: #fff;
                font-size: 16px;
                font-weight: 700;
            }
        }
    }
}
